// Heading
.heading {
  z-index: 1;
  .heading__myname {
    margin-bottom: 24px;
    .heading__myname__ball {
      top: -8px;
      left: 12px;
      transition: all 1s ease;
      transform: scale(0,1);
      span {
        border-color: #FFF;
        color: #FFF;
      }
    }
  }
  .heading__welcome {
    visibility: hidden;
    opacity: 0;
    margin-bottom: 12px;
    color: #FFF;
    font-size: 24px;
    text-align: center;
    transition: all 1s ease;
  }
  .heading__nav {
   height: 0;
   overflow: hidden;  
   transition: all 1s ease;
   button {
     transition: all .3s ease;
     &:hover {
        transform: scale(1.1,1.1);
     }
   }
  }
  .heading__progress {
    transition: all 1s ease;
  }
  .heading__bg {
    position: absolute;
    top:0; left:0;
    width:100%; height:100%;
    background-color: hsl(0,0%,80%);
    z-index: -1;
    transition: all 1s ease;
  }
}

// After Opening
body.load-done .heading.finish {
  .heading__myname {
    .heading__myname__ball {
      transform: scale(1,1);
    }
  }
  .heading__welcome {
    visibility: visible;
    opacity: 1;
    top: 0;
  }
  .heading__nav {
    height: 200px;
  }
  .heading__bg {
    visibility: hidden;
    opacity:0;
  }
  .heading__nav {
    svg path {
      animation: svgTrim 2s ease 1s forwards;
      &#p8 {
        animation: svgTrimP8 2s ease 1s forwards;
      }
      &#circle {
        animation: svgTrimCircle .3s ease 2s forwards;
      }
    }
  }
  .heading__progress {
    visibility: hidden;
    opacity:0;
  }
}

// SVG Animation
@keyframes svgTrim {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes svgTrimP8 {
  to {
    stroke-dasharray: 20 10;
      stroke-dashoffset: -6;
  }
}
@keyframes svgTrimCircle {
  to {
    opacity: 1;
  }
}

// My Name
.myname {
  display: block;
  svg {
    width: 560px;
    height: 190px;
    path {
      -webkit-clip-path: url(#glenn);
      clip-path: url(#glenn);
      fill: none;
      stroke: #FFF;
      stroke-width: 26px;
      stroke-linecap: round;
      stroke-linejoin: round;
      &#g {
        stroke-dasharray: 470;
        stroke-dashoffset: 470;
          animation: svgTrim 1s ease .5s forwards;
      }
      &#l {
        stroke-dasharray: 190;
        stroke-dashoffset: 190;
          animation: svgTrim .4s ease 1.5s forwards;
      }
      &#e {
        stroke-dasharray: 240;
        stroke-dashoffset: 240;
          animation: svgTrim .6s ease 1.9s forwards;
      }
      &#n1 {
        stroke-dasharray: 270;
        stroke-dashoffset: 270;
          animation: svgTrim .6s ease 2.5s forwards;
      }
      &#n2 {
        stroke-dasharray: 270;
        stroke-dashoffset: 270;
          animation: svgTrim .6s ease 3.1s forwards;
      }
    }
  }
}

// Nav buttom
